
import { createContext, useEffect, useReducer } from "react";
import TaskService from "../../Services/TaskService";
import taskReducer from "../../store/reducer/taskReducer";
import { errorMessage, successMessage } from "../../pages/utils/Alert";


export const TaskContext = createContext(null);
const initialState = {
    listModel: [], showModal: false
    , model: { id: 0, taskName: '', duration: 0, statusId: 1 },
    selectedStatusModel: null, error: false, errorMessage: ''
};

const TaskProvider = ({ children }) => {

    const [state, dispatch] = useReducer(taskReducer, initialState);

    const { listModel, showModal, model, selectedStatusModel, error, errorMessage } = state;


    const service = new TaskService();

    useEffect(() => {
        getTaskList();
    }, []);

    const getTaskList = async () => {

        try {
            let list = await service.Search();
            dispatch({ type: "setListModel", payload: list });
        }
        catch (err) {
            ManageError(dispatch, err);
        }
    }
    const registerTask = async () => {

        try {
            if (model.id === 0) {

                let newTask = await service.Register(model);
                dispatch({ type: "register", payload: newTask.id });
                dispatch({ type: "hideModal" });
                successMessage();

            }
            else {
                await service.Edit(model);

                dispatch({ type: "register" });
                dispatch({ type: "hideModal" });
                successMessage();
            }
        }
        catch (err) {
            ManageError(dispatch, err);
        }
    }

    const openTaskFormForEdit = (task) => {
        dispatch({ type: "showModal" });
        dispatch({ type: "selectModel", payload: task });
    }

    const removeTask = async (id) => {
        try {
            await service.Remove(id);
            dispatch({ type: "remove", payload: id });
        }
        catch (err) {
            ManageError(dispatch, err);
        }
    }

    const showTaskForm = () => dispatch({ type: "showModal" });


    const hideTaskForm = () => dispatch({ type: "hideModal" })

    const openTaskFormForRegister = () => {
        dispatch({ type: "showModal" });
        dispatch({ type: "resetModel" });

    }

    const getTaskCountbyStatusId = (statusId) => {

        let list = listModel.filter(p => p.statusId === statusId);
        return list.length;
    }

    const searchTaskCountbyStatusId = (statusId) => {
        let list = listModel.filter(p => p.statusId === statusId);
        return list;
    }

    const setSelectedTaskStatusModel = (status) => {
        dispatch({ type: "selectStatus", payload: status });
    }

    const isExistsTask = (taskName) => {
        let list = listModel.filter(p => p.taskName === taskName.trim()
            && p.id !== model.id
        );

        return list.length > 0;
    }

    return (
        <TaskContext.Provider value={{
            listModel, showModal, model, selectedStatusModel, error, errorMessage
            , registerTask, openTaskFormForEdit, removeTask, showTaskForm
            , hideTaskForm, openTaskFormForRegister, getTaskCountbyStatusId
            , setSelectedTaskStatusModel, searchTaskCountbyStatusId, isExistsTask
        }}>

            {children}
        </TaskContext.Provider>

    )
}


export default TaskProvider;

function ManageError(dispatch, err) {
    dispatch({ type: "showError", payload: err.message });
    errorMessage(err.message);
    setTimeout(() => {
        dispatch({ type: "hideError" });
    }, 5000);
}
