import { Get, Post, Put ,Remove} from "../adapter/Api"
import { getTaskListAction, registerTaskAction, removeTaskAction } from "../adapter/ServerAction"

export default class TaskService
{

    Search=()=>{
   
       return Get(getTaskListAction);
    }

    Register=(model)=>
    {
       return Post(registerTaskAction,model);
    }

    Edit=(model)=>
    {
        return Put(registerTaskAction,model);
    }

    Remove=(id)=>
    {
       return Remove(removeTaskAction,{id:id});
    }
}